import React from "react";

const MidBannerOne = ({ className }) => {
  return (
    <section className={className}>
      <h4>
        {"EasySocial chatbot talks to your customers, "}
        <br />
        {"when you are busy working on orders."}
      </h4>
    </section>
  );
};

export default MidBannerOne;
